import { random_element, remove_random, clone_reg_exp } from "./util";

export class Sentence {
    template: string;

    constructor(template: string) {
        this.template = template;
    }

    populate(vocabulary: { [pattern: string]: Array<string> }): string {
        let res = this.template.slice();
        for (let [p, choices] of Object.entries(vocabulary)) {
            let pattern = new RegExp(p);
            let indices = Array.from(Array(choices.length).keys());
            for (let i = this.template.matchAll(clone_reg_exp(pattern, "g")); !i.next().done;) {
                let i = remove_random(indices) as number;
                res = res.replace(pattern, choices[i]);
            }
        }
        return res;
    }
}

export class LangModuleMask {
    enabled: Array<boolean>;
    sentences: Array<boolean>;
    vocabulary: Array<boolean>;

    constructor(enabled: Array<boolean>, sentences: Array<boolean>, vocabulary: Array<boolean>) {
        this.enabled = enabled;
        this.sentences = sentences;
        this.vocabulary = vocabulary;
    }
}

export class LangModule {
    name: string;
    sentences: Array<Sentence>;
    vocabulary: { [pattern: string]: Array<string> };

    constructor(name: string, sentences: Array<Sentence>, vocabulary: { [pattern: string]: Array<string> }) {
        this.name = name;
        this.sentences = sentences;
        this.vocabulary = vocabulary;
    }

    static empty(): LangModule {
        return new LangModule("Empty", [], {});
    }

    static from_json(json: any): LangModule {
        let name = json.name;
        let sentences = json.sentences.map(s => new Sentence(s));
        let vocabulary = json.vocabulary;
        return new LangModule(name, sentences, vocabulary);
    }

    static join(modules: Array<LangModule>, mask: LangModuleMask): LangModule {
        let res = new LangModule("Joined", [], {});
        modules.forEach((m, i) => {
            if (mask.enabled[i]) {
                if (mask.sentences[i]) {
                    res.sentences = res.sentences.concat(m.sentences);
                }
                if (mask.vocabulary[i]) {
                    for (let [p, c] of Object.entries(m.vocabulary)) {
                        if (p in res.vocabulary) {
                            res.vocabulary[p] = res.vocabulary[p].concat(c);
                        } else {
                            res.vocabulary[p] = c;
                        }
                    }
                }
            }
        });

        return res;
    }

    random_with_vocabulary(vocabulary: { [pattern: string]: Array<string> }): string {
        if (this.sentences.length > 0) {
            let s: Sentence = random_element(this.sentences);
            return s.populate(vocabulary);
        } else {
            return "";
        }
    }

    random() {
        return this.random_with_vocabulary(this.vocabulary);
    }
}
