import React, { useState, useEffect } from "react";
import { InputGroup, FormControl, Container, Row, Col, Card, Accordion, Button, ButtonGroup, ToggleButton } from "react-bootstrap";
import { LangModule, LangModuleMask } from "./lang_module";
import "./App.scss";

// Default source for language module data
const DEFAULT_SOURCE = "https://raw.githubusercontent.com/nilsso/nilsso.github.io/code/static/apps/lang_modules/japanese_modules.json";

// Helper functions
const mask = (length: number, fill: boolean) => Array(length).fill(fill);
const toggle_mask = (mask: Array<boolean>, i: number, value: boolean) => {
    let res = [...mask];
    res[i] = value;
    return res;
};

// Fetch ("GET" request) file from URL and parse a JSON
async function requestJSON(url: string) {
    var module_data;
    await fetch(url)
        .then(response => response.json())
        .then(data => module_data = data)
        .catch(e => alert("Invalid source URL\n" + e));
    if (module_data) {
        return module_data;
    } else {
        return [];
    }
}

function App() {
    // Module source
    const [source, setSource] = useState(DEFAULT_SOURCE);
    // Modules
    const [modules, setModules] = useState(Array<LangModule>(0));
    // Joined module
    const [joined, setJoined] = useState(LangModule.empty());
    // Module masks
    const [enabled, setEnabled] = useState(mask(0, false));
    const [sentences, setSentences] = useState(mask(0, true));
    const [vocabulary, setVocabulary] = useState(mask(0, true));

    // If module source changed build new modules
    useEffect(() => {
        requestJSON(source).then(data => setModules(data.map(json => LangModule.from_json(json))));
    }, [source]);

    // If modules changed update the module masks
    useEffect(() => {
        setEnabled(mask(modules.length, false));
        setSentences(mask(modules.length, true));
        setVocabulary(mask(modules.length, true));
    }, [modules]);

    // If module masks changed update the joined module
    useEffect(() => {
        if (modules.length > 0) {
            setJoined(LangModule.join(modules, new LangModuleMask(enabled, sentences, vocabulary)));
        }
    }, [modules, enabled, sentences, vocabulary]);

    return (
        <Container>
            <Row>
                <Generator joined={joined}/>
                <ModulesComponent
                    modules={modules}

                    enabled={enabled}
                    sentences={sentences}
                    vocabulary={vocabulary}

                    setEnabled={setEnabled}
                    setSentences={setSentences}
                    setVocabulary={setVocabulary}
                />
            </Row>
            <Source source={source} setSource={setSource}/>
        </Container>
    );
}

function Source(props: any) {
    const source = props.source;
    const setSource = props.setSource;

    const [value, setValue] = useState(source);

    return (
        <Row>
            <Col className="component">
                <InputGroup>
                    <InputGroup.Prepend>
                        <InputGroup.Text>
                            Module Source
                        </InputGroup.Text>
                    </InputGroup.Prepend>
                    <FormControl
                        defaultValue={value}
                        onChange={e => setValue(e.target.value)}
                    />
                    <InputGroup.Append>
                        <Button
                            variant="outline-secondary"
                            onClick={() => setSource(value)}
                        >
                            Load
                        </Button>
                    </InputGroup.Append>
                </InputGroup>
            </Col>
        </Row>
    );
}

function Generator(props: any) {
    const joined = props.joined;

    // Generated sentence or expresson
    const [generated, setGenerated] = useState("...");

    return (
        <Col className="component">
            <Card>
                <Card.Header>
                    <span className="header-text">Generator</span>
                    <Button
                        onClick={() => setGenerated(joined.random())}
                    >
                        Generate
                    </Button>
                </Card.Header>
                <Card.Body>
                    <h1>{generated}</h1>
                </Card.Body>
            </Card>
        </Col>
    );
}

function ModulesComponent(props: any) {
    // Modules state
    const modules = props.modules;
    // Mask states
    const enabled = props.enabled;
    const sentences = props.sentences;
    const vocabulary = props.vocabulary;
    // Mask state setters
    const setEnabled = props.setEnabled;
    const setSentences = props.setSentences;
    const setVocabulary = props.setVocabulary;

    // Helper to toggle all modules
    const toggleAll = function(checked: boolean) {
        setEnabled(mask(modules.length, checked));
    }

    let module_components = modules.map((lm: LangModule, i: number) => {
        return <ModuleComponent
            key={i}
            name={lm.name}

            enabled={enabled[i]}
            sentences={sentences[i]}
            vocabulary={vocabulary[i]}

            setEnabled={(checked: boolean) => setEnabled(toggle_mask(enabled, i, checked))}
            setSentences={(checked: boolean) => setSentences(toggle_mask(sentences, i, checked))}
            setVocabulary={(checked: boolean) => setVocabulary(toggle_mask(vocabulary, i, checked))}
        />;
    });

    return (
        <Col className="component">
            <Card>
                <Card.Header>
                    <span className="header-text">Modules</span>
                    <Button
                        variant="primary"
                        onClick={() => toggleAll(true)}
                    >
                        Enable All
                    </Button>
                    <Button
                        variant="danger"
                        onClick={() => toggleAll(false)}
                    >
                        Enable None
                    </Button>
                </Card.Header>
                <Card.Body>
                    <div id="module-list">{module_components}</div>
                </Card.Body>
            </Card>
        </Col>
    );
}

function ModuleComponent(props: any) {
    // Mask states
    const enabled = props.enabled;
    const sentences = props.sentences;
    const vocabulary = props.vocabulary;
    // Mask state setters
    const setEnabled = props.setEnabled;
    const setSentences = props.setSentences;
    const setVocabulary = props.setVocabulary;

    return(
        <Accordion className="module">
            <Card>
                <Card.Body id="module-control">
                    <ButtonGroup
                        toggle className="module-controls"
                    >
                        <ToggleButton
                            type="checkbox"
                            variant="outline-primary"
                            value={0}
                            checked={enabled}
                            onChange={(e) => setEnabled(e.currentTarget.checked)}
                        >
                            {props.name}
                        </ToggleButton>
                        <ToggleButton
                            type="checkbox"
                            variant="outline-secondary"
                            value={1}
                            checked={sentences}
                            disabled={!enabled}
                            onChange={(e) => setSentences(e.currentTarget.checked)}
                        >
                            Sentences
                        </ToggleButton>
                        <ToggleButton
                            type="checkbox"
                            variant="outline-secondary"
                            value={2}
                            checked={vocabulary}
                            disabled={!enabled}
                            onChange={(e) => setVocabulary(e.currentTarget.checked)}
                        >
                            Vocabulary
                        </ToggleButton>
                    </ButtonGroup>
                </Card.Body>
            </Card>
            <Card>
                <Accordion.Toggle
                    as={Card.Header}
                    className="module-contents-header small"
                    variant="link"
                    eventKey="0"
                >
                    Contents
                </Accordion.Toggle>
                <Accordion.Collapse eventKey="0">
                    <Card.Body>
                        Sentences, Vocabulary
                    </Card.Body>
                </Accordion.Collapse>
            </Card>
        </Accordion>
    );
}

export default App;
